}

.auth-box-w form .form-check-inline {
  margin-left: 1rem;
}

.auth-box-w form .form-group {
  position: relative;
}

.auth-box-w form .form-group .pre-icon {
  position: absolute;
  bottom: 5px;
  font-size: 24px;
  color: #047bf8;
  left: -38px;
}

.auth-box-w .buttons-w {
  padding-top: 1rem;
  margin-top: 0.5rem;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}

.auth-box-w.centered {
  text-align: center;
}

.auth-box-w.centered .auth-header {
  padding-left: 0px;
}

.auth-box-w.centered .auth-header:after {
  left: 50%;
  transform: translateX(-50%);
  left: 50%;
}

/*
 #13. Timed Activities Element
*/
.timed-activities {
  margin-bottom: 1rem;
}

.timed-activity {
  display: flex;
  margin-bottom: 2rem;
  font-size: 1.08rem;
}

.timed-activity .ta-date {
  flex: 0 0 130px;
  color: rgba(0, 0, 0, 0.4);
  position: relative;
  text-align: center;
}

.timed-activity .ta-date span {
  position: relative;
  display: inline-block;
}

.timed-activity .ta-date span:after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -30px;
  width: 16px;
  height: 16px;
  border: 3px solid #C76DC8;
  border-radius: 8px;
  background-color: #fff;
}

.timed-activity .ta-date:before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -20px;
  width: 1px;
  top: 70px;
  background-color: rgba(0, 0, 0, 0.1);
}

.timed-activity .ta-record-w {
  padding-left: 40px;
}

.timed-activity .ta-record {
  display: flex;
  margin-bottom: 10px;
  align-items: flex-start;
}

.timed-activity .ta-timestamp {
  position: relative;
  padding-right: 40px;
  color: rgba(0, 0, 0, 0.4);
  width: 110px;
  white-space: nowrap;
}

.timed-activity .ta-timestamp strong {
  color: #3E4B5B;
}

.timed-activity .ta-timestamp:after {
  content: '\e910';
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'osfont' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(0, 0, 0, 0.3);
  font-size: 10px;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-40%);
  display: block;
}

.timed-activity .ta-activity a {
  display: inline-block;
  padding-bottom: 1px;
  border-bottom: 1px solid #3b75e3;
}

.timed-activity .ta-activity a:hover {
  text-decoration: none;
  color: #1747a1;
  border-bottom-color: #1747a1;
}

.timed-activities.compact .timed-activity {
  display: block;
  padding-left: 30px;
  position: relative;
}

.timed-activities.compact .timed-activity:before {
  position: absolute;
  width: 1px;
  background-color: rgba(0, 0, 0, 0.1);
  bottom: 0px;
  top: 45px;
  content: "";
  left: 8px;
}

.timed-activities.compact .timed-activity .ta-date {
  flex: 0;
  text-align: left;
  margin-bottom: 1.5rem;
}

.timed-activities.compact .timed-activity .ta-date:before {
  display: none;
}

.timed-activities.compact .timed-activity .ta-date span:after {
  left: -30px;
  top: 50%;
  bottom: auto;
  right: auto;
  transform: translateY(-50%);
}

.timed-activities.compact .timed-activity .ta-record-w {
  padding-left: 20px;
}

.timed-activities.compact .timed-activity .ta-record {
  display: block;
}

.timed-activities.compact .timed-activity .ta-timestamp {
  font-size: 0.81rem;
  margin-bottom: 5px;
}

.timed-activities.compact .timed-activity .ta-timestamp:after {
  right: auto;
  left: -20px;
  font-size: 8px;
}

.timed-activities.compact .timed-activity .ta-activity {
  font-size: 0.9rem;
  margin-bottom: 10px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

